const styles={
    nav:{
        height:'15vh',
        width:'100vw',
        backgroundColor:'rgba(233, 233, 233, 1)'
    },
    heading:{
        fontFamily: 'Inter',
        fontSize: 48,
        fontWeight: 800,
        letterSpacing: '0em',
        lineHeight:2,
        textAlign: 'left',
        marginLeft:40,
    }
}

const Header=()=>{
    return(
        <>
            <div style={styles.nav}>
                <p style={styles.heading}>Equaim Blog</p>
            </div>
        </>
    )
}

export default Header;