const styles={
    blog:{
        marginTop:80,
        marginLeft:60,
        height:180,
        width:300,
        backgroundColor:'rgba(217, 217, 217, 1)',
        display:'flex',
        justifyContent:'center',
        alignItems:'center',
        borderRadius:20,
    },
    title:{
        fontFamily: 'Inter',
        fontSize: 32,
        fontWeight: 400,
        letterSpacing: '0em',
        
    }
    
}

const Blog=({data})=>{
    return(
        <>
            <div style={styles.blog}>
            <p style={styles.title}>{data.title}</p>
            </div>   
        </>
    )
}

export default Blog;            