import Home from '../../Public/Images/Home.png';
import Publish from '../../Public/Images/Publish.png';
import RichTextEditor from './TextEditor';
import {useState} from 'react';
import { Link } from "react-router-dom";
import { create_blog } from '../../api';
import { useNavigate } from "react-router-dom";

const styles = {
    main: {
        display: 'flex',
        flexDirection:'row',
        width: '100vw',
    },
    leftBlock: {
        flex: 1,
        margin: 10,

    },
    image: {
        margin: 10,
        width: 80,
        height: 70,
        backgroundColor: 'rgba(233, 233, 233, 1)',
        borderRadius: 30,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        cursor:'pointer'
    }
    ,
    rightBlock: {
        flex: 8,
        margin:"30px 50px ",
        backgroundColor: 'rgba(233, 233, 233, 1)',
        width:'100%'
    },
    title:{
    marginLeft:'30vw',
    size:40,
    height:30,
    fontSize:20,
    textAlign:'center',
    borderRadius:2
    }

}

const PublishBlog = () => {
    const [value,setValue]=useState('This is a blog Post');
    const [title,setTitle]=useState('Blog Title');
    const navigate = useNavigate();

    const handleChange=(e)=>{
        setTitle(e.target.value);
    }

    const savePost=async()=>{
        console.log("Clicked");
        const res=await create_blog({title,data:value});
        console.log(res);
        if(res.data.blog_created)
        navigate('/');

    }
    return (
        <>
            <div className="Left Block" style={styles.main}>
                <div style={styles.leftBlock}>

                    <Link to='/'>
                    <div className="Home_Icon" style={styles.image}>
                        <img src={Home} ></img>
                    </div>
                    </Link>


                    <div className="Publish_Icon" style={styles.image} onClick={()=>savePost()}>
                        <img src={Publish} ></img>
                    </div>

                </div>

                <div className="Right Block" style={styles.rightBlock}>
                    <input style={styles.title} value={title} onChange={(e)=>handleChange(e)}/>
                    <div className="main" styles={styles.content}>
                    <RichTextEditor initialValue={value} setValue={setValue} /> 
                    </div>
                </div>
            </div>
        </>
    )
}

export default PublishBlog;