import Home from '../../Public/Images/Home.png';
import Publish from '../../Public/Images/Publish.png';
import {useState,useEffect} from 'react';
import { Link } from "react-router-dom";
import { get_blog } from '../../api';
import { useParams} from "react-router-dom";

import parse from 'html-react-parser';

const styles = {
    main: {
        display: 'flex',
        flexDirection:'row',
        width: '100vw',
    },
    leftBlock: {
        flex: 1,
        margin: 10,

    },
    image: {
        margin: 10,
        width: 80,
        height: 70,
        backgroundColor: 'rgba(233, 233, 233, 1)',
        borderRadius: 30,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
    }
    ,
    rightBlock: {
        flex: 8,
        margin:"30px 50px ",
        backgroundColor: 'rgba(233, 233, 233, 1)',
        width:'100%'
    },
    title:{
    marginLeft:'30vw',
    size:40,
    height:30,
    fontSize:20,
    textAlign:'center',
    borderRadius:2
    }

}

const BlogPost = () => {
    const [data,setData]=useState(false);
    const params=useParams();
    
    const get_data=async()=>{
        setData(await get_blog(params.id));
    }

    useEffect(()=>{
        get_data();
    },[]);

    console.log(data);

    return (
        <>
            <div className="Left Block" style={styles.main}>
                <div style={styles.leftBlock}>

                    <Link to='/'>
                    <div className="Home_Icon" style={styles.image}>
                        <img src={Home} ></img>
                    </div>
                    </Link>

                </div>

                <div className="Right Block" style={styles.rightBlock}>
                   {
                    !data? <></>:<>
                        {
                            <>
                            <h1 style={{textAlign:'center'}}>{data.title}</h1>
                            {<p style={{marginLeft:10}}>{parse(data.data)}</p>}
                            </>
                        }
                    </>
                   }
                </div>
            </div>
        </>
    )
}

export default BlogPost;