import axios from 'axios';

export const get_blogs=async()=>{
    try{
        const data=await axios.get('/api/blogs');
        return data.data;
    }
    catch(e){
        console.log(e);
    }
}

export const create_blog=async(data)=>{
    try{
        const res=await axios.post('/api/create',data);
        return res;
    }
    catch(e){
        console.log(e);
    }
}

export const get_blog=async(id)=>{
    try{
        const res=await axios.get(`/api/blog?id=${id}`);
        return res.data;
    }
    catch(e){
        console.log(e);
    }
}
