import Blog from './Blog';
import create from '../../Public/Images/Create.png';
import { Link } from "react-router-dom";
import {useEffect,useState} from 'react';
import { get_blogs } from '../../api';

const styles={
    blogBox:{
        width:'100vw',
        display:'flex',
        flexDirection:'row',
        flexWrap:'wrap'
    },
    image: {
        margin: 10,
        width: 80,
        height: 70,
        backgroundColor: 'rgba(233, 233, 233, 1)',
        borderRadius: 30,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
    },
    publish:{
        position:'fixed',
        margin:20,
        bottom:20,
        right:20
    },
       title:{
        fontFamily: 'Inter',
        fontSize: 32,
        fontWeight: 400,
        letterSpacing: '0em',
        
    }
}
const Home = () => {
    const [blogs,setBlogs]=useState([]);

    const GET_BLOGS=async()=>{
        setBlogs(await get_blogs());
    };

    useEffect(()=>{
       GET_BLOGS();
    },[]);

    console.log(blogs);
    return (
        <>
            <div className="blogBox" style={styles.blogBox}>
                {blogs.length===0 ? <></>:<>{
                    blogs.map(blog=>

                    <Link style={{textDecoration:'none',...styles.title,color:'black'}} to={`/view/${blog._id}`}><Blog data={blog}></Blog></Link>
                    )
                }</>}
                
            </div>
            <Link to='/publish'>
            <div className="Publish_Icon" style={{...styles.image,...styles.publish}}>
                    <img src={create}></img>
            </div>
            </Link>
        </>
    )
}

export default Home;